import { Formik, ErrorMessage } from 'formik';
import * as Yup from 'yup';
import { Notify } from 'notiflix';

import { IoIosCall, IoMdPerson, IoMdPersonAdd } from 'react-icons/io';

import { useGetContactsQuery, useAddContactMutation } from 'redux/contactsAPI';

import {
  PhonebookForm as Form,
  Input,
  Label,
  Error,
} from './AddContactForm.styled';
import Box from 'components/shared/Box';
import Button from 'components/shared/Button';
import theme from 'theme';

const phoneRegExp =
  /^((\\+[1-9]{1,4}[ \\-]*)|(\\([0-9]{2,3}\\)[ \\-]*)|([0-9]{2,4})[ \\-]*)*?[0-9]{3,4}?[ \\-]*[0-9]{3,4}?$/;

const ValidationSchema = Yup.object().shape({
  name: Yup.string()
    .min(2, 'Too Short!')
    .max(20, 'Too Long!')
    .required('Required'),
  tel: Yup.string()
    .matches(phoneRegExp, 'Number is not valid')
    .required('Required'),
});

const AddContactForm = () => {
  const { data: myContacts = [] } = useGetContactsQuery();

  const [
    addContact,
    {
      isLoading: isContactAddLoading,
      isSuccess: isContactAddSuccec,
      isError: isContactAddError,
      error: ContactaddError,
    },
  ] = useAddContactMutation();

  const handleContactAdd = contact => {
    const { name } = contact;

    const normalizedName = name.toLowerCase().trim();

    const dublicate = myContacts.find(({ name }) => {
      return name.toLowerCase().trim() === normalizedName;
    });

    if (dublicate) {
      Notify.failure(`${name} already in contacts`, {
        showOnlyTheLastOne: true,
        position: 'right-top',
      });
      return;
    } else {
      addContact(contact);
    }
  };

  if (isContactAddSuccec) {
    Notify.success('Contact added successfully');
  }

  if (isContactAddError) {
    Notify.failure(ContactaddError);
  }

  return (
    <Formik
      initialValues={{
        name: '',
        number: '',
      }}
      validationSchema={ValidationSchema}
      onSubmit={({ name, tel }, { resetForm }) => {
        const contact = {
          name: name.trim(),
          number: tel.trim(),
        };

        handleContactAdd(contact);
        resetForm();
      }}
    >
      <Form>
        <Box
          display="flex"
          flexDirection="column"
          alignItems="flex-start"
          mr={[4]}
        >
          <Box display="flex" alignItems="center">
            <IoMdPerson size={34} color={theme.colors.accent} />
            <Box position="relative">
              <Input id="name" name="name" placeholder="Name" />
              <ErrorMessage name="name" component={Error} />
              <Label htmlFor="name">Name</Label>
            </Box>
          </Box>
        </Box>

        <Box
          display="flex"
          flexDirection="column"
          alignItems="flex-start"
          mr="auto"
        >
          <Box display="flex" alignItems="center">
            <IoIosCall size={34} color={theme.colors.accent} />
            <Box position="relative">
              <Input
                id="tel"
                name="tel"
                placeholder="Number: 000000000000"
                type="tel"
              />
              <ErrorMessage name="tel" component={Error} />
              <Label htmlFor="tel">Phone</Label>
            </Box>
          </Box>
        </Box>

        <Button
          type="submit"
          isLoading={isContactAddLoading}
          icon={IoMdPersonAdd}
          disabled={isContactAddLoading ? true : false}
          children="Add contact"
          iconSize={20}
        />
      </Form>
    </Formik>
  );
};

export default AddContactForm;
